<template>
  <transition name="slide">
    <div class="user-center">
      <div class="back" @click="back">
        <i class="icon-back"></i>
      </div>
      <div class="switches-wrapper">
        <switches :switches="switches" :currentIndex="currentIndex" @select="selectSwitch"></switches>
      </div>
      <div ref="playBtn" class="play-btn" @click.stop="random">
        <i class="icon-play"></i>
        <span class="text">随机播放全部</span>
      </div>
      <div ref="listWrapper" class="list-wrapper">
        <scroll ref="favoriteList" class="list-scroll" v-if="this.currentIndex===0" :data="favoriteList">
          <div class="list-inner">
            <song-list :songs="favoriteList" @select="selectSong"></song-list>
          </div>
        </scroll>
        <scroll ref="playList" class="list-scroll" v-if="this.currentIndex===1" :data="playListHistory">
          <div class="list-inner">
            <song-list :songs="playListHistory" @select="selectSong"></song-list>
          </div>
        </scroll>
      </div>
      <div class="no-result-wrapper" v-show="resultShow">
        <noresult :title="noResultDes"></noresult>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Switches from 'base/switches/switches'
  import Scroll from 'base/scroll/scroll'
  import SongList from 'base/song-list/song-list'
  import { mapGetters, mapActions } from 'vuex'
  import Song from 'common/js/song'
  import { playListMixin } from 'common/js/mixin'
  import Noresult from 'base/noresult/noresult'

  export default {
    mixins: [playListMixin],
    data() {
      return {
        switches: [
          {name: '我喜欢的'},
          {name: '最近听的'}
        ],
        currentIndex: 0
      }
    },
    computed: {
      resultShow() {
        if (this.currentIndex === 0) {
          return !this.favoriteList.length
        } else {
          return !this.playListHistory.length
        }
      },
      noResultDes() {
        if (this.currentIndex === 0) {
          return '您还没有收藏歌曲'
        } else {
          return '您最近还没有听过歌曲'
        }
      },
      ...mapGetters([
        'playListHistory',
        'favoriteList'
      ])
    },
    methods: {
      handlePlayList(playlist) {
        let bottom = playlist.length > 0 ? '60px' : ''
        this.$refs.listWrapper.style.bottom = bottom
        this.$refs.favoriteList && this.$refs.favoriteList.refresh()
        this.$refs.playList && this.$refs.playList.refresh()
      },
      random() {
        let list = this.currentIndex === 0 ? this.favoriteList : this.playListHistory
        if (list.length === 0) {
          return
        }
        list = list.map((item) => {
          return new Song(item)
        })

        this.playRandom({list})
      },
      back() {
        this.$router.back()
      },
      selectSwitch(index) {
        this.currentIndex = index
      },
      selectSong(song) {
        this.addSong(new Song(song))
      },
      ...mapActions([
        'addSong',
        'playRandom'
      ])
    },
    components: {
      Switches,
      Scroll,
      SongList,
      Noresult
    }
  }
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .user-center
    position: fixed
    top: 0
    bottom: 0
    z-index: 100
    width: 100%
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .switches-wrapper
      margin: 10px 0 30px 0
    .play-btn
      box-sizing: border-box
      width: 135px
      padding: 7px 0
      margin: 0 auto
      text-align: center
      border: 1px solid  $color-text-l
      color: $color-text-l
      border-radius: 100px
      font-size: 0
      .icon-play
        display: inline-block
        vertical-align: middle
        margin-right: 6px
        font-size: $font-size-medium-x
      .text
        display: inline-block
        vertical-align: middle
        font-size: $font-size-small
    .list-wrapper
      position: absolute
      top: 110px
      bottom: 0
      width: 100%
      .list-scroll
        height: 100%
        overflow: hidden
        .list-inner
          padding: 20px 30px
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>


